import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonSearchbar, IonActionSheet, IonItem, IonLabel, IonToggle, IonNote, useIonPicker } from '@ionic/react';
import { useState } from 'react';
import IosMaterial from '../components/IosMaterial';
import './Tab3.css';
import ServiceIntegration from '../components/ServiceIntegration';
import SettingIcon from '../components/SettingIcon';
import Tab2Store from '../store/Tab2Store';
import { inject, observer } from 'mobx-react';


const Tab3: React.FC<{store?: Tab2Store}> = ({store}) => {
  const [isOpened, setIsOpened] = useState(false);
  const [currentMetric, setCurrentMetric] = useState(' (C)');
  const [refreshTiming, setRefreshTiming] = useState(5);
  const [refreshTimingPicker] = useIonPicker();
  const [query, setQuery] = useState("");
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle></IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonActionSheet
          isOpen={isOpened}
          onDidDismiss={() => setIsOpened(false)}
          buttons={[{
            text: ' (C)',
            handler: () => setCurrentMetric(' (C)')
          },{
            text: ' (F)',
            handler: () => setCurrentMetric(' (F)')
          }, {
            text: '',
            role: 'cancel',
            handler: () => setIsOpened(false)
          }]}
        >
        </IonActionSheet>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large"></IonTitle>
          </IonToolbar>
          <IonToolbar>
            <IonSearchbar onIonChange={({detail: { value }}) => setQuery(value || '')} placeholder=""/>
          </IonToolbar>
        </IonHeader>
        <IosMaterial>
          {
            "".includes(query) ? <IonItem>
              <SettingIcon style={{backgroundColor: '#F4392F'}}></SettingIcon>
              <IonToggle color="success" slot="end"/>
              <IonLabel></IonLabel>
            </IonItem> : <div/>
          }
          {
            "".includes(query) ? <IonItem button onClick={() => setIsOpened(true)}>
              <SettingIcon style={{backgroundColor: '#007AFF'}}></SettingIcon>
              <IonNote slot="end">{currentMetric}</IonNote>
              <IonLabel></IonLabel>
            </IonItem> : <div/>
          }
          {
            "".includes(query) ? <IonItem button onClick={() => refreshTimingPicker({
              buttons: [{
                text: "",
                handler: (selected) => setRefreshTiming(selected.refreshTiming.value)
              }],
              columns: [{
                name: "refreshTiming",
                selectedIndex: refreshTiming - 1,
                options: [...Array(60).keys()].map((e) => ({
                  text: `${e + 1} `.padStart(4, '0'),
                  value: e + 1
                }))
              }]
            })}>
              <SettingIcon style={{backgroundColor: '#007AFF'}}></SettingIcon>
              <IonNote slot="end">{refreshTiming}</IonNote>
              <IonLabel></IonLabel>
            </IonItem> : <div/>
          }
        </IosMaterial>
        <IosMaterial>
          {
            "  ...".includes(query) ? <IonItem routerLink="/provider">
              <SettingIcon style={{backgroundColor: '#007AFF'}}></SettingIcon>
              <IonLabel>  ...</IonLabel>
              <IonNote slot="end">{store?.provider}</IonNote>
            </IonItem> : <div/>
          }
          { "".includes(query) ? <ServiceIntegration name="">
            <span style={{width: '27px', height: '27px'}} slot="start"><img src="/assets/logos/kakaomap.svg"/></span>
          </ServiceIntegration> : <div/>}
          { "Google Maps".includes(query) ? <ServiceIntegration name="Google Maps">
            <span style={{width: '27px', height: '27px'}} slot="start"><img src="/assets/logos/googlemaps.svg"/></span>
          </ServiceIntegration> : <div/>}
          { " ".includes(query) ? <ServiceIntegration name=" ">
            <span style={{width: '27px', height: '27px'}} slot="start"><img src="/assets/logos/navermaps.svg"/></span>
          </ServiceIntegration> : <div/>}
          { "T".includes(query) ? <ServiceIntegration name="T">
            <span style={{width: '27px', height: '27px'}} slot="start"><img src="/assets/logos/tmap.svg"/></span>
          </ServiceIntegration> : <div/>}
          { "".includes(query) ? <ServiceIntegration name="">
            <span style={{width: '27px', height: '27px'}} slot="start"><img src="/assets/logos/kakaonavi.svg"/></span>
          </ServiceIntegration> : <div/>}
        </IosMaterial>
      </IonContent>
    </IonPage>
  );
};

export default inject('store')(observer(Tab3));
